import React, { Component } from 'react'

const Link = ({ link, description }) => (
  <div className="bg-grey p-4 w-48 h-16 rounded-sm border border-black-100 border-solid">
    <a className="font-sm" target="_blank" href={link}>
      <span>{link}</span>
    </a>
      <p>{description}</p>
  </div>
)

class LinkList extends Component {
  render() {
    return (
      <div>
        {this.props.links.map(({ link, description }) => (
          <Link {...{ link, description }} />
        ))}
      </div>
    )
  }
}
export default LinkList
