import React from 'react'
import AddNewLink from '../components/AddNewLink'
import LinkList from '../components/LinkList'

class IndexPage extends React.Component {
  state ={
    links:[]
  }

  componentDidMount(){
    let links = localStorage.getItem('links');
    links = JSON.parse(links);
    if(links){ this.setState({links}) };
  }

  addLink = ({link,description}) => {
     this.setState({links:[...this.state.links,{link,description}]},
     () => localStorage.setItem('links',JSON.stringify(this.state.links))
     )
  }

  render(){
  return(
    <div>
      <AddNewLink addLink={this.addLink}/>
      <LinkList links={this.state.links}/>
    </div>
  );
  }
}

export default IndexPage
