import React, { Component } from 'react';

class AddNewLink extends Component {
  state = {
    value: ''
  }

  handleSubmit = (e) => {
     e.preventDefault();
     this.props.addLink({description: 'description', link: this.state.value})
  }

  handleChange = (e) => {
     this.setState({value: e.target.value})
  }

  render() {
    return (
      <form className="p-4" onSubmit={this.handleSubmit}>
      <input className="mr-4 border-2 border-solid" onChange={this.handleChange}/>
      <input type="submit" value="Dodaj"/>
      </form>
    );
  }
}
export default AddNewLink
